  0% {
    background-color: #f4f0ec;
  }

  10% {
    background-color: #ece9e6e1;
  }

  20% {
    background-color: #e7e5e2ce;
  }

  30% {
    background-color: #d8d6d38f;
  }

  40% {
    background-color: #c9c6c38f;
